body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to left,  #162744 16%,#1b2d56 29%,#173c77 39%,#173c77 42%,#173c77 42%,#173c77 42%,#1e5799 52%,#1e5799 54%,#207cca 62%,#207cca 62%,#207cca 64%,#207cca 64%,#2989d8 75%,#539952 96%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
